@import url(https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap);
html {
  scroll-behavior: smooth;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.App {
  text-align: center;
  overflow: hidden;
  height: 100%;
  width: 100vw;
}

.update {
  position: absolute;
  top: 5px;
  left: 5px;
  color: white;
  text-shadow: 1px 1px black;
  z-index: 3;
}

.header{height:5vw;width:100%;background:transparent;position:absolute;top:0;z-index:2;padding-right:3%}.header ul{width:100%;height:100%;display:flex;justify-content:flex-end}.header ul li{color:#fff;font-family:"Gochi Hand",cursive;text-shadow:2px 2px 2px #000;font-weight:bold;font-size:2vw;padding:2% 3%;transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s}.header ul li .nav-link{text-decoration:none}.header ul li .nav-link:visited{color:#fff;text-decoration:none}.header ul li:hover{-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}
.homepage,.about-me,.landing-screen,.tech{height:100vh}.homepage{-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory;overflow:auto}.homepage .about-me,.homepage .landing-screen,.homepage .tech{scroll-snap-align:start}
.landing-screen{height:100vh;width:100%;display:flex;justify-content:center;align-items:center;background:linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.1)),url("https://images.pexels.com/photos/440731/pexels-photo-440731.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260");background-repeat:no-repeat;background-size:100% 100%;overflow:hidden}.container{display:grid;grid-template-columns:1fr .5fr 1fr;grid-template-rows:repeat(6, 1fr);grid-column-gap:0px;grid-row-gap:0px;height:30vh;width:80vw;font-family:"Gochi Hand",cursive;font-size:5vw;text-shadow:#000 5px 5px 5px;color:#fff}.div-name{grid-area:3/1/5/2;-webkit-animation:fromLeft 2s forwards 1s;animation:fromLeft 2s forwards 1s;opacity:0}.div-dev{grid-area:3/3/5/4;-webkit-animation:fromRight 2s forwards 1s;animation:fromRight 2s forwards 1s;opacity:0}.div-lottie{grid-area:3/2/5/3;-webkit-animation:fromBottom .5s forwards .5s;animation:fromBottom .5s forwards .5s;margin-top:-35%;opacity:0}@-webkit-keyframes fromLeft{from{opacity:0;-webkit-transform:translateX(-500px);transform:translateX(-500px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes fromLeft{from{opacity:0;-webkit-transform:translateX(-500px);transform:translateX(-500px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@-webkit-keyframes fromRight{from{opacity:0;-webkit-transform:translateX(500px);transform:translateX(500px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes fromRight{from{opacity:0;-webkit-transform:translateX(500px);transform:translateX(500px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@-webkit-keyframes fromBottom{from{opacity:0;-webkit-transform:translateY(500px);transform:translateY(500px)}to{opacity:1;-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes fromBottom{from{opacity:0;-webkit-transform:translateY(500px);transform:translateY(500px)}to{opacity:1;-webkit-transform:translateY(0px);transform:translateY(0px)}}
.tech{width:100%;overflow:hidden;position:relative}.tech .tech-video{max-height:150%;min-height:100%;max-width:150%;min-width:100%;z-index:1;position:absolute;bottom:0;right:0}.tech .overlay{position:absolute;background-color:rgba(0,0,0,.7);width:80%;height:75%;left:10%;top:15%;opacity:0;border-radius:10px;border:groove #fff 5px;z-index:2;-webkit-animation:appear 1s forwards .5s;animation:appear 1s forwards .5s}.tech-content{display:flex;justify-content:center;align-items:flex-start;position:absolute;color:#fff;font-size:3vw;padding-top:2vw;width:80%;height:75%;left:10%;top:15%;z-index:3;-webkit-animation:appear 1s forwards .5s;animation:appear 1s forwards .5s}.tech-content h1{color:#fff;font-size:2vw;width:22ch;overflow:hidden;border-right:3px solid #fff;-webkit-animation:typing 1.5s steps(22) 1s forwards,blink .5s step-end 2s infinite alternate,invisible 1s;animation:typing 1.5s steps(22) 1s forwards,blink .5s step-end 2s infinite alternate,invisible 1s;white-space:nowrap}@-webkit-keyframes invisible{from{width:0;border-right:none}to{width:0;border-right:none}}@keyframes invisible{from{width:0;border-right:none}to{width:0;border-right:none}}@-webkit-keyframes appear{from{opacity:0;height:0}to{opacity:1;height:75%}}@keyframes appear{from{opacity:0;height:0}to{opacity:1;height:75%}}@-webkit-keyframes typing{from{width:0}}@keyframes typing{from{width:0}}@-webkit-keyframes blink{50%{border-color:transparent}}@keyframes blink{50%{border-color:transparent}}
.section{width:10%;opacity:0;border-radius:7vw;position:relative;margin:0 1%;color:#fff;text-shadow:#000 2px 2px 2px;text-align:center;box-shadow:0 0 2vw .5vw #1e1e1e;transition:width 1s,background-size 1s,border-radius 1s,height 1s,margin 1s;background-size:auto 110%;background-repeat:no-repeat;padding-top:3%;display:flex;flex-direction:column;justify-content:space-between}@-webkit-keyframes alive{0%{height:0}20%{height:120%;opacity:1}50%{height:100%}100%{height:120%;opacity:1}}@keyframes alive{0%{height:0}20%{height:120%;opacity:1}50%{height:100%}100%{height:120%;opacity:1}}.section:nth-child(1){background-image:url("https://images.pexels.com/photos/2434267/pexels-photo-2434267.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260");background-position:80%;-webkit-animation:alive 1s forwards;animation:alive 1s forwards}.section:nth-child(2){background-image:url("https://images.pexels.com/photos/4253831/pexels-photo-4253831.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260");background-position:45%;-webkit-animation:alive 1s forwards .3s;animation:alive 1s forwards .3s}.section:nth-child(3){background-image:url("https://images.pexels.com/photos/248559/pexels-photo-248559.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260");background-position:20%;-webkit-animation:alive 1s forwards .6s;animation:alive 1s forwards .6s}.section:nth-child(4){background-image:url("https://images.pexels.com/photos/5650704/pexels-photo-5650704.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");background-position:50%;-webkit-animation:alive 1s forwards .9s;animation:alive 1s forwards .9s}.section:hover{width:55%;height:50vh;background-size:auto 150%;border-radius:5vw;padding-top:1%}.section>.section-title h1,.section-title h3,.section-infos{display:block;opacity:0;font-size:20%;transition:opacity .3s,font-size .3s}.section-title h1{transition:font-size 1s}.section-title h3{transition:font-size 1s}.section:hover .section-title{opacity:1}.section:hover .section-title h1{opacity:1;font-size:300%}.section:hover .section-title h3{opacity:1;font-size:200%}.section:hover .section-infos{height:30%;background-color:rgba(0,0,0,.6);border-radius:0 0 5vw 5vw;z-index:2;font-family:Verdana,Geneva,Tahoma,sans-serif;line-height:150%;font-size:1vw;font-size-adjust:inherit;padding:4% 10%;list-style:none;display:flex;flex-direction:column;justify-content:space-around;-webkit-animation:coming 1s forwards .5s;animation:coming 1s forwards .5s}@-webkit-keyframes coming{from{opacity:0}to{opacity:1}}@keyframes coming{from{opacity:0}to{opacity:1}}
.about-me{font-family:"Gochi Hand",cursive;width:100%;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;background:linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)),url("https://images.pexels.com/photos/6775290/pexels-photo-6775290.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260");background-repeat:no-repeat;background-size:100% 100%}.about-me-title{height:35%}.about-me-title h1{font-size:600%;color:#fff;text-shadow:.5vw .5vw 1vw #000}.sections{width:80%;height:40%;display:flex;justify-content:center;align-items:center}
